<template>
    <span class="count">
        <!-- <span class="minus-to" v-if="foodArr.filter(item=>JSON.stringify(item)==JSON.stringify(foodItem)).length" @click.stop="minusNum"></span>
        <span class="num" v-if="foodArr.filter(item=>JSON.stringify(item)==JSON.stringify(foodItem)).length">{{(foodArr.filter(item=>JSON.stringify(item)==JSON.stringify(foodItem)).length)}}</span>
        <span class="add-to" @click.stop="addNum"></span> -->
        <span class="minus-to" v-if="filterFoodArr.length" @touchend.stop="minusNum"></span>
        <span class="num" v-if="filterFoodArr.length">{{(filterFoodArr.length)}}</span>
        <span class="add-to" @touchend.stop="addNum"></span>
    </span>
</template>

<script>
import {mapMutations,mapState} from 'vuex'
export default{
    props:['foodItem'],
    data(){
        return{
            foodArr:[],
            filterFoodArr:[]
        }
    },
    methods:{
        addNum(){
            this.foodArr.push(this.foodItem);
            this.changeCartContent(this.foodArr);
        },
        minusNum(){
            let index = this.foodArr.findIndex(item => JSON.stringify(item) == JSON.stringify(this.foodItem));
            this.foodArr.splice(index,1);
            if(this.foodArr.length == 0){
                this.$emit('isShowCart',false);
            }
            this.changeCartContent(this.foodArr);   
        },
        ...mapMutations(['changeCartContent'])
    },
     computed:{
        ...mapState(['cartList'])
    },
    created(){
        this.foodArr = [].concat(this.cartList);
        this.filterFoodArr = this.foodArr.filter(item=>JSON.stringify(item)==JSON.stringify(this.foodItem));
    },
    watch:{
        cartList(val){
            this.foodArr = [].concat(val);
        },
        foodArr(val){
            this.filterFoodArr = this.foodArr.filter(item=>JSON.stringify(item)==JSON.stringify(this.foodItem));
        }
    },
}
</script>

<style scoped lang="scss">
    @import "@/assets/css/public/addMinus.scss"
</style>